.AnswersItem {
    border: 1px solid #fff;
    border-radius: 5px;
    padding: 5px 10px;
    margin-bottom: 5px ;
    cursor: pointer;
}

.AnswersItem:hover{
    background: rgba(255, 255, 255, .2);
    transition: background .2s ease-in-out;
}

.AnswersItem.success {
    background:rgba(161, 240, 69, .7)
}

.AnswersItem.error {
    background:rgba(240, 87, 108, 0.7)
}