.QuestCreator {
    display: flex;
    justify-content: center;
    padding-top: 50px;
    flex-grow: 1;
    width: 100%;
    background: linear-gradient(270deg, #f0613c 0%, #f0783c 100%);
}

.QuestCreator > div {
    width: 100%;
    max-width: 800px;
    padding: 0 20px;

}

.QuestCreator h1 {
    text-align: center;
}

.QuestCreator form {
    background: #eee;
    border-right: 5px;
    padding: 15px;
    box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
}